#main{
    width: 100%;
    /* border: 1px solid red; */
    background: rgb(208,208,250);
    background: linear-gradient(90deg, rgba(208,208,250,1) 0%, rgba(214,160,223,1) 35%, rgb(232, 238, 163) 97%, rgb(235, 235, 163) 100%);
}
#main>h2{
    color: rgb(13, 13, 51);
   /* margin-top: 3%; */
   padding-top: 2%;

}

#main>div{
    width: 90%;
    margin: auto;
    /* border: 1px solid rgb(0, 255, 55); */
    background-color: rgb(252, 246, 246);
    margin-top: 2%;
    display: flex;
    flex-wrap: wrap;
  justify-content: space-around;
}

#main>div>div{
    text-align: left;
    padding: 1%;
